:root {
    --icon-image-width: 70%;
    --image-container-height: 50px;
    /* --width-spacing: 10%; */
}

.SkillIcon {
    text-align: center;
    width: var(--width-spacing);
    margin-bottom: 5%;
    margin: 20px;
}

/* 
.SkillIcon-image {
    min-width: 20%;
    max-width:80%;
}  */

.SkillIcon-image-container {
    display:flex;
    margin: 2.5px;
    justify-content:center;
    align-content: center;
    height: var(--image-container-height);
}

.SkillIcon-name {
    text-align: center;
    margin-top: 20px;
    font-size: 150%;
}


/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    :root { 
        --width-space: 20%;
    }
    .SkillIcon-name {
        font-size: 100%;
    }

    .SkillIcon-image-container {
        height: 50px
    }

    .SkillIcon-image {
        max-width: 70%;
    }

}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {

}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
    :root {
        --width-space: 5%;
    }
    
}

